import util from './common/util.js'
import resource from './common/resource.js'

function StateInfo() {
  this.enemies = {
    all: {},
    resolve: {}
  }
  this.emitShot = {
    all: {},
    resolve: {}
  }
  this.spoils = {
    all: {},
    resolve: {}
  }
  this.currentShotNum = 0
  this.currentEnemyNum = 0
  this.getDebugArray = function () {
    var sumLog = function (obj) {
      var keys = Object.keys(obj)
      var sum = 0
      for (var i = 0; i < keys.length; i++) {
        sum += obj[keys[i]]
      }
      return sum
    }
    var arr = []

    var allKillEmemies = sumLog(this.enemies.resolve)
    var allEnemies = sumLog(this.enemies.all)
    arr.push('' + allKillEmemies + '\r\n')
    arr.push('' + allEnemies + '\r\n')
    arr.push('' + allKillEmemies / allEnemies + '\r\n')

    var allTargetShots = sumLog(this.emitShot.resolve)
    var allShots = sumLog(this.emitShot.all)
    arr.push('' + allTargetShots + '\r\n')
    arr.push('' + allShots + '\r\n')
    arr.push('' + allTargetShots / allShots + '\r\n')

    arr.push('' + this.currentShotNum + '\r\n')
    arr.push('' + this.currentEnemyNum + '\r\n')
    return arr
  }
  this.reset = function () {
    this.enemies.all = {}
    this.enemies.resolve = {}

    this.emitShot.all = {}
    this.emitShot.resolve = {}

    this.spoils.all = {}
    this.spoils.resolve = {}

    this.currentShotNum = 0
    this.currentEnemyNum = 0
  }
}

function DebugSetting() {
  this.isDebug = {
    message: '',
    value: false
  }
  this.stageTime = {
    message: '',
    value: 20 // unit ms
  }
  this.spoilRate = {
    message: '',
    value: 0.5
  }
  this.enemyRemoveRate = {
    message: '',
    value: 0.5
  }
  this.enemyShotRate = {
    message: '',
    value: 0.5
  }
  this.playerShotRate = {
    message: '',
    value: 20
  }
  this.spoilRate = {
    message: '',
    value: 0.5
  }
}

module.exports = {
  StateInfo: StateInfo,
  DebugSetting: DebugSetting
}


